export class Sort {

    protected data: any[] = [];

    public static make(): Sort {
        return new Sort();
    }

    public asc(field: string, mode: string = ''): this {
        return this.add(field, 'asc', mode);
    }

    public desc(field: string, mode: string = ''): this {
        return this.add(field, 'desc', mode);
    }

    protected add(field: string, order: string, mode: string): this {

        let obj: any = {}
        obj[field] = { order: order };
        if (mode) obj[field].mode = mode;
        this.data.push(obj)

        return this;
    }

    public toJSON() {
        return this.data;
    }
}